import React, {useState} from 'react';
import shortURL from 'node-url-shortener';
import {FaCopy, FaTrash} from "react-icons/fa";


const Home = () => {

    const [inputValue, setInputValue] = useState('');
    const [outputURL, setOutputURL] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState('');
    const [copied, setCopied] = useState(false);

    const handleCopy = (e) =>{
        setCopied(true);
        e.preventDefault();
        navigator.clipboard.writeText(outputURL);
        setTimeout(hideCopyMessage, 2000);
        
    }

    const hideCopyMessage = () => setCopied(false);
     const handleClear = (e)=>{
         e.preventDefault();
         setOutputURL('');
     }


    const handleClick = (e) =>{
        setIsLoading(true);
        setError('');
        e.preventDefault();
        try{
        if (inputValue)
        {
            if (inputValue.includes('www.') && (inputValue.includes('.in') || inputValue.includes('.com')
                || inputValue.includes('.org') || inputValue.includes('.net')))
            {
               
            
                shortURL.short(inputValue, function(err, url){
                
                console.log(url);
                setOutputURL(url);
                });
            }
            else
            {
                setError('Please enter a valid URL!');
            }
        }}
        catch (ex)
        {
            
        }
        finally{
            setIsLoading(false);
        }
        
    }

    return ( 
    <div className="home-container">
        <h1>URL Shortener</h1>
        {isLoading && <h3>Loading...</h3>}
        {error && <h4 className="error-msg">{error}</h4>}
        { !isLoading &&  <div>
            <div className="input-combo">
            <input type="text" id="inputURL" placeholder="Please enter the URL" value={inputValue} onChange={(e)=> setInputValue(e.target.value)}></input>
            <button onClick={handleClick} className="btn-submit">Click here</button>
            </div>
           {  outputURL && <div className="input-icons"> <input type="text" id="outputURL" className="input-fields" disabled value={outputURL}></input>
                <div className="btn-combo">
                    <button onClick={handleCopy} className="tooltip"><FaCopy/><span className="tooltiptext">Copy to clipboard</span></button>
                    <button onClick={handleClear} className="tooltip">
                    <FaTrash/> <span className="tooltiptext">Clear</span></button></div>
                { copied && <p className="copy-message">Copied</p>}
           </div>
           
           }
        </div>}


      </div>
     );
}
 
export default Home;